.about {
  padding: 125px 20px 0 20px;
  background-color: $background-color-darker;
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center 75%;
  background-image: url(../images/milk-mobile.png);

  @include tablet {
    & {
      background-image: none;
      padding: 130px 16px 0;
    }
  }
  @include desktop {
    & {
      background-image: url(../images/milk-desktop.png);
      margin: 0 auto;
      padding: 125px 92px 0;
    }
  }

  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    & {
      background-image: url(../images/milk-mobile@2x.png);
    }
    @include tablet {
      & {
        background-image: none;
      }
    }
    @include desktop {
      & {
        background-image: url(../images/milk-desktop@2x.png);
        margin: 0 auto;
        padding: 125px 92px 0;
      }
    }
  }
  @media (min-device-pixel-ratio: 3), (min-resolution: 268dpi), (min-resolution: 3dppx) {
    & {
      background-image: url(../images/milk-mobile@3x.png);
    }
    @include tablet {
      & {
        background-image: none;
      }
    }
    @include desktop {
      & {
        background-image: url(../images/milk-desktop@3x.png);
        margin: 0 auto;
        padding: 125px 92px 0;
      }
    }
  }
  &__subtitle {
    text-align: center;
    font-family: $fontSecondary;
    font-size: 18px;
    line-height: 1.17;
    font-weight: 400;
    text-align: center;
    text-transform: uppercase;
    color: $paragraph-color;
    margin-bottom: 15px;
    @include tablet {
      margin-bottom: 16px;
      font-size: 26px;
      line-height: 1.15;
    }
    @include desktop {
      margin-bottom: 15px;
      font-size: 30px;
      line-height: 1.13;
      letter-spacing: 0.64px;
    }
    &--modal {
      color: $paragraph-color;
      text-shadow: 0px 0px 11px rgba(0, 0, 0, 0.47);
    }
  }
  &__title {
    font-family: $fontSecondary;
    font-size: 34px;
    line-height: 1.15;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 0.62px;
    color: $active-color;
    margin-bottom: 26px;
    @include tablet {
      font-size: 48px;
      letter-spacing: 0.64px;
      margin-bottom: 50px;
    }
    @include desktop {
      font-size: 58px;
      letter-spacing: 0.96px;
      margin-bottom: 55px;
    }
  }
  &__img {
    display: flex;
    max-width: 100%;
    align-items: center;
    justify-content: center;
    margin-bottom: 46px;
  }
  &__small-block {
    padding: 0 50px;
    @include tablet {
      padding: 50px 0 0 17px;
    }
    @include desktop {
      padding: 0 5px 0 74px;
    }
  }
  @include tablet {
    &__big-block {
      display: flex;
      align-items: flex-start;
      @include desktop {
        align-items: center;
      }
    }
  }
}

.list {
  font-family: $fontPrimary;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.62;
  color: $text-color-darker;
  @include tablet {
    font-size: 14px;
    line-height: 1.76;
    margin-bottom: 62px;
  }
  @include desktop {
    margin-bottom: 45px;
  }
  &--modal {
    margin-bottom: 0;
    color: #615442;
  }
  &__item {
    padding-bottom: 14px;
    letter-spacing: 0.64px;
    @include desktop {
      line-height: 1.94;
    }
    &--marked {
      color: $text-color-bold;
      @include tablet {
        padding-bottom: 35px;
        letter-spacing: normal;
      }
      @include desktop {
        font-size: 16px;
        line-height: 1.76;
        letter-spacing: 0.64px;
        padding-bottom: 36px;
      }
    }
  }
}
.button {
  font-family: $fontPrimary;
  font-weight: 700;
  line-height: 1.3;
  border-radius: 22px;
  border-color: transparent;
  padding: 15px 36px;
  text-align: center;
  color: $active-color;
  background-color: $text-color-lighter;

  &::first-letter {
    text-transform: capitalize;
  }
  &:focus,
  &:hover {
    cursor: pointer;
    background-color: $button-active;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }
  &__about {
    margin-bottom: 66px;
  }
  &__icon {
    margin-left: 14px;
    vertical-align: middle;
  }
}
.modal {
  position: fixed;
  top: 0;
  left: 0;
  background-color: #fff5f633;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  transition-property: visibility, opacity, transform;
  transition-duration: 1s;
  z-index: 999;

  &--hidden {
    pointer-events: none;
    visibility: hidden;
    opacity: 0;
    animation-name: aboutModalClose;
    animation-duration: 2000ms;
    animation-timing-function: linear;
  }
}
.window {
  width: 450px;
  background-color: $background-color-darker;
  box-shadow: 0px 1px 3px #0000001f, 0px 1px 1px #00000024, 0px 2px 1px #00000033;
  box-shadow: 64px -21px 109px -2px rgba(212, 20, 67, 0.75);
  -webkit-box-shadow: 64px -21px 109px -2px rgba(212, 20, 67, 0.75);
  -moz-box-shadow: 64px -21px 109px -2px rgba(212, 20, 67, 0.75);
  border-top-right-radius: 100px;
  border-bottom-left-radius: 100px;
  border: transparent;
  position: relative;
  padding: 40px;
  animation-name: aboutModalClose;
  animation-duration: 1000ms;
  animation-direction: reverse;
  animation-timing-function: linear;

  @include desktop {
    width: 530px;
  }
}

.button-close {
  border: $accent-color;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  border-bottom-left-radius: 50px;
  width: 30px;
  height: 30px;
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
  &__icon {
    fill: $accent-color;
    background-color: $background-color-darker;
    border-radius: 50%;
    padding: 6px;
    margin: 0;
  }
}
@keyframes aboutModalClose {
  0% {
    transform: scale(1);
  }
  25% {
    transform: scale(0.75);
  }
  50% {
    transform: scale(0.5);
  }
  100% {
    transform: scale(0.25);
  }
}
